@import 'settings';
@import 'fonts';

.fusion-secondary-main-menu{
    background: $color-purple;
    border: none !important;
    border-color: none !important;
}
.fusion-main-menu > ul > li > a {
    text-transform: uppercase;
}
.fusion-header-v4 .fusion-header {
    padding: 8px 0;
}
.searchform .search-table .search-button input[type="submit"]{
    background: $color-orange;
    width: 50px;
}
 #wrapper .search-table .search-field input{
    border: 1px solid #7d7d7d;
    border-right: none;
 }
 .fusion-social-networks .fusion-social-networks-wrapper{
    margin: 0;
    margin-left: 30px;
 }
 .fusion-social-networks .fusion-social-network-icon {
    background: #d7d7d7;
    padding: 2px 7px;
    text-align: center;
    font-size: 15px;
    line-height: 24px;
    margin: 0 4px;
    border-radius: 20px;

    &.fusion-facebook{
        background: #3e4da0;
    }
    &.fusion-twitter{
        background: #589fd5;
    }
    &.fusion-googleplus{
        background: #db4628;
    }
    &.fusion-instagram{
        background: #9b519e;
    }
    &.fusion-youtube{
        background: #da4726;
    }

 }
 .fusion-header-v4 .fusion-logo .fusion-header-content-3-wrapper{
    display: block;
    width: 70%;
    float: right;
    text-align: right;

    .katalogus-header-link, .head-search{
        float: right;
    }
    .primary-header-line{
        .katalogus-header-link{
            a {
                background: $color-orange;
                color: white;
                line-height: 29px;
                padding: 0 15px;
                text-transform: uppercase;
                font-weight: 700;
                font-size: 15px;
                letter-spacing: 1px;
                text-shadow: none;
                margin-right: 25px;
                -webkit-transition: all .4s;
                -moz-transition: all .4s;
                transition: all .4s;

                &:hover {
                    background: $color-purple;
                }
            }
        }
    }
    .secondary-header-line{
        margin-top: 25px;

        .fusion-secondary-menu-social, .contact-phone {
            float: right;
        }
        .contact-phone {
            ul {
                margin: 0; padding: 0;
                li {
                    color: $color-orange;
                    line-height: 28px;
                    font-size: 18px;
                    display: inline-block;
                    *display: inline;
                    zoom: 1;
                    &.sep {
                        padding: 0 10px;
                    }
                }
            }
        }
    }
 }
 .fusion-main-menu > ul > li:last-child{
   padding-right: 0 !important;
 }
.copy-info-box{
  background: lighten($color-purple, 20%);
  padding: 15px 0;
  text-align: center;
  font-size: 12px;
  color: lighten($color-purple, 50%);
}
.fusion-megamenu-title{
  padding: 0;
}

.fusion-megamenu-wrapper .fusion-megamenu-submenu{
  padding: 0;
  height: $megamenu-height;
}
.fusion-megamenu-title{
  a{
    display: block;
    height: $megamenu-height;
  }
}
.fusion-megamenu-icon{
  height: $megamenu-height;
  width: 100%;
  position: absolute;
  overflow: hidden;

  img {
    width: 100%;
    height: $megamenu-height;
    max-height: $megamenu-height;
  }
}
.fusion-footer-widget-area .current-menu-item > a {
  color: white !important;
}
.fusion-widget-area .widget_nav_menu li {
  padding: 6px 0;
}
.fusion-footer-widget-area a{
  text-align: left;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 1;
  &:before {
    display: none;
  }
  &:hover {
    color: white !important;
  }
}

.fusion-widget-area > div.fusion-column {
  border-right: 1px solid white;
  height: 140px;
  padding: 0 40px;
}
.fusion-widget-area > div.fusion-column-last {
  border-right: none;
  a {
    text-transform: none !important;
  }
}
#menu-labresz-bal{
  li {
    width: 50%;
    float: left;
  }
}

#wrapper #main{
 .fusion-post-content > h2.entry-title > a{
   color: white;
   text-transform: uppercase;
   font-weight: bold;
 }
 .fusion-read-more{
   background: $color-orange;
   color: white;
   text-transform: uppercase;
   padding: 10px 25px 10px 15px;
   font-size: 12px;
   margin: 0;

   &:after {
     color: white;
   }
 }
}

.fusion-blog-layout-grid{
  .fusion-post-content-wrapper{
    padding: 20px 0 0 0;
  }
  .post {
    .fusion-content-sep{
      display: none;
    }
  }
  .fusion-post-content-container{
    font-size: 12px;
    color: rgba(255, 255, 255, 0.8);
  }
}

.boxed-title{
  h2.box-title{
    background: #d7d7d7;
    width: 355px;
    margin: 0 auto;
    text-align:center;
    color: white;
    text-transform: uppercase;
    font-size: 18px !important;
    font-weight: bold !important;
    padding: 10px;
  }

  &.title-color-purple {
    h2.box-title {
      background: $color-purple;
    }
  }
  &.title-color-orange {
    h2.box-title {
      background: $color-orange;
    }
  }
  &.title-up{
    h2.box-title{
      margin-top: -23px;
    }
  }
}

.couple-in-cloud{
  position: absolute;
  left: -380px;
  bottom: -20px;
  pointer-events: none;
  img {
    width: 500px;
    max-width: 500px;
  }
}

.program-box-cloud{
  height: 580px;
  max-height: 580px;
  overflow: hidden;
}

