                }
              }
            }
          }

        }
      }

    }
  }
}
.sziget-korok-holder{
  display: flex;
  align-items: baseline;

  .circle {
    flex-basis: 25%;
    text-align: center;
    padding: 20px 10px;
    position:relative;

    .circle-inside {
      border: 5px solid #acacac;
      position: relative;
      overflow: hidden;
      height: 250px;
      width: 250px;
      margin: 0 auto;
      background: #d7d7d7;
      border-radius: 200px;
      z-index: 20;

      .title {
        padding: 10px 0;
        background: #aaa;
        color: white;
        position: absolute;
        width: 100%;
        left: 0;
        bottom: 20px;
        font-size: 22px;
        text-transform: uppercase;
        pointer-events: none;
        text-shadow: 0 2px 3px rgba(0, 0, 0, 0.4);
      }
    }

    &.circle-tenerife {
      .circle-inside {
        border: 5px solid $color-tenerife;
        .title {
          background: $color-tenerife;
        }
      }
    }

    &.circle-gran-canaria {
      .circle-inside {
        border: 5px solid $color-gran-canaria;
        .title {
          background: $color-gran-canaria;
        }
      }
    }

    &.circle-fuerteventura {
      .circle-inside {
        border: 5px solid $color-fuerteventura;
        .title {
          background: $color-fuerteventura;
        }
      }
    }

    &.circle-lanzarote {
      .circle-inside {
        border: 5px solid $color-lanzarote;
        .title {
          background: $color-lanzarote;
        }
      }
    }
  }

  .circle:hover {
    .image img {
      -webkit-transform: scale(1.2);
      -moz-transform: scale(1.2);
      transform: scale(1.2);
      -webkit-filter: blur(2px);
      -moz-filter: blur(2px);
      -o-filter: blur(2px);
      -ms-filter: blur(2px);
      filter: blur(2px);
    }
    .title {
      bottom: 50%;
      padding: 20px 0;
      -webkit-transform:translateY(50%);
      -moz-transform:translateY(50%);
      transform:translateY(50%);
    }
  }

